import { createStore } from 'vuex';

export default createStore({
  state: {
    readyToPrint: "false",
    htmlToPrint: "",
  },
  getters: {
    readyToPrint(state) {
      return state.readyToPrint === "true" ? true : false;
    },
    htmlToPrint(state) {
      return state.htmlToPrint;
    }
  },
  mutations: {
    setReadyToPrint(state, value) {
      state.readyToPrint = value ? "true" : "false";
    },
    setHTMLToPrint(state, value) {
      state.htmlToPrint = value;
    },
  },
  actions: {
  },
  modules: {
  }
})
