import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import PDFGeneratorView from '../views/PDFGeneratorView.vue';
import PDFPrinterView from '../views/PDFPrinterView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/pdf-generator',
    name: 'pdf-generator',
    component: PDFGeneratorView
  },
  {
    path: '/pdf-printer',
    name: 'pdf-printer',
    component: PDFPrinterView
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
